@media screen and (max-width: 1000px) {
    #firstPage{
        .firstPageContent{
            .title{
                h2{
                    font-size: 28px;
                }
                h3{
                    font-size: 22px;
                }

            }
        }
    }

    .topNav{
        >ul{left: -40px; transform: translate(0,0);}
        >ul li {font-size: 15px;}
    }
    .about{
        >h2{
            font-size: 32px;
        }
        .leftContent{width: 80%;}
        .rightContent{
            .skillIndividual{
                .skillTitle{
                    width: 70px;
                    font-size: 12px;
                    padding: 3px;
                }
                .skillProficiency{
                    font-size: 12px;
                    padding: 3px;
                }
            }
        }

        .skillImage{
            h4{font-size: 20px;}
        }
    }

}