.footer{
    width: 100%;
    margin-top: 50px;
    color:white;
    z-index: 99;

    img.divider{
        width: 100%;
        height: 100px;
        
    }

    .rowContainer{background-color: rgb(37,41,52);padding-bottom: 40px;}
    .row{
        width: 80%;
        margin:auto;
    }

    .logoLeft{
        position: relative;
        img{
            width: 100px;
            position: absolute;
            right:20px;

        }
    }
    ul li{
        list-style-type: none;
        font-size:16px;
    }
    h2{
        font-family: 'Courier New', Courier, monospace;
        font-size: 20px;
    }
    .section1,.section2,.section3{
        margin: auto;
        margin-top: 0;
    }

    .bottomFooter{
        background-color:rgba(37,41,52,0.9);
        padding-top: 10px;
        height: 50px;
    }
}